import * as types from './mutation-types'

export default {
  //  
  [types.PRE_PAGE] (state, offset) {
    state.offset -= offset
  },
  //  
  [types.NEXT_PAGE] (state, offset) {
    state.offset += offset
  },
  //  
  [types.GO_PAGE] (state, offset) {
    state.offset = offset
  }
}



